import React, { Component } from 'react'

class ClassClick extends Component {

    ClickHandler(){
        console.log("Class ClickHandler 1 :");
    }

    render() {
        return (
            <div>
                <p>This is event handling on class components </p>
                <button onClick={this.ClickHandler}>
                    Class Event 
                </button>
            </div>
        )
    }
}

export default ClassClick
