import React,{Component} from 'react'

class Welcome extends Component{
    constructor(props){
        super(props)
    }
    render(){
        return(
            <div>
                <p>This is class Component with Welcome Greetings :)</p>
                <div>
                    <h3>Hello {this.props.name} aka {this.props.Hero}</h3>
                    {this.props.children}
                </div>
            </div>
        )
    }
}

export default Welcome